$card-width: 200px;
$card-number: 4;
$card-margin-x: 48px;
$card-margin-y: 46px;
$catalog-padding-x: 40px;
$catalog-padding-y: 50px;

$catalog-width: ($card-width * $card-number)
  + (($card-number - 1) * $card-margin-x)
  + (2 * $catalog-padding-x);

$dark-blue-color: #060b35;
$grey-color: #616070;
$blue-color: #00acdc;
$white-color: white;
